<template>
  <div>
    <router-link to="/login">Already have account?</router-link>
    <form>
      <input type="text" placeholder="Full name" class="input_data" id="name" name="name" required>
      <input type="email" placeholder="email" class="input_data" id="email"  name="email" required>
      <input type="text" placeholder="Password" class="input_data" id="password" name="password"  pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,16}" required>
      <input type="submit" value="Sign Up" class="sign_up_button" id="sign_up_button_value" >
    </form>
    <router-link to="/">Home</router-link>
  </div>
</template>
